  private formatDateYYYYMMDD(date: Date): string {
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    return `${year}${month}${day}`;
  }

  checkExpiringProducts(): void {
    const today = new Date();
    const threeMonthsLater = new Date(today.getTime() + 90 * 24 * 60 * 60 * 1000);

    // ìœ íš¨ê¸°ê°„ ì„ë°• ì œí’ˆ (3ê°œì›” ì´ë‚´)
    this.expiringLots = this.lotBatches.filter(lot => {
      return lot.expiryDate <= threeMonthsLater && lot.expiryDate > today && lot.currentStock > 0;
    });

    // ìœ íš¨ê¸°ê°„ ê²½ê³¼ ì œí’ˆ
    this.expiredLots = this.lotBatches.filter(lot => {
      return lot.expiryDate <= today && lot.currentStock > 0;
    });

  }

  // ===== ìœ íš¨ê¸°ê°„ ê´€ë¦¬ í•¨ìˆ˜ =====
  
  expiryTab: 'expired' | 'expiring' | 'all' = 'expiring';

  getDaysExpired(expiryDate: Date): number {
    const today = new Date();
    const diffTime = today.getTime() - expiryDate.getTime();
    return Math.floor(diffTime / (1000 * 60 * 60 * 24));
  }

  getDaysUntilExpiry(expiryDate: Date): number {
    const today = new Date();
    const diffTime = expiryDate.getTime() - today.getTime();
    return Math.floor(diffTime / (1000 * 60 * 60 * 24));
  }

  getExpiryDaysDisplay(expiryDate: Date): string {
    const days = this.getDaysUntilExpiry(expiryDate);
    if (days < 0) {
      return `${Math.abs(days)}ì¼ ê²½ê³¼`;
    }
    return `${days}ì¼ ë‚¨ìŒ`;
  }

  isExpired(expiryDate: Date): boolean {
    return expiryDate <= new Date();
  }

  isExpiring(expiryDate: Date): boolean {
    const today = new Date();
    const threeMonthsLater = new Date(today.getTime() + 90 * 24 * 60 * 60 * 1000);
    return expiryDate > today && expiryDate <= threeMonthsLater;
  }

  getExpiryStatus(expiryDate: Date): string {
    if (this.isExpired(expiryDate)) return 'ìœ íš¨ê¸°ê°„ ê²½ê³¼';
    if (this.isExpiring(expiryDate)) return 'ìœ íš¨ê¸°ê°„ ì„ë°•';
    return 'ì •ìƒ';
  }

