  // ===== ì˜¨ìŠµë„ ê´€ë¦¬ í•¨ìˆ˜ =====

  tempZoneFilter: string = 'all';
  tempDateRange: string = 'week';
  tempShowAbnormalOnly: boolean = false;
  filteredTemperatureRecords: TemperatureHumidityRecord[] = [];

  getZoneIcon(zoneType: 'room' | 'refrigerated' | 'frozen' | 'quarantine'): string {
    switch(zoneType) {
      case 'room': return 'ğŸ ';
      case 'refrigerated': return 'â„ï¸';
      case 'frozen': return 'ğŸ§Š';
      case 'quarantine': return 'âš ï¸';
      default: return 'ğŸ“¦';
    }
  }

  getCurrentReading(zoneId: string): TemperatureHumidityRecord {
    // ê°€ì¥ ìµœê·¼ ì¸¡ì • ë°ì´í„° ë°˜í™˜
    const zoneRecords = this.temperatureRecords.filter(r => r.zoneId === zoneId);
    return zoneRecords.sort((a, b) => b.recordDate.getTime() - a.recordDate.getTime())[0] || {
      id: '',
      zoneId,
      temperature: 0,
      humidity: 0,
      recordDate: new Date(),
      recordedBy: '',
      deviceId: '',
      status: 'normal',
      alertTriggered: false
    };
  }

  getZone(zoneId: string): StorageZone {
    return this.storageZones.find(z => z.zoneId === zoneId) || this.storageZones[0];
  }

  getZoneName(zoneId: string): string {
    return this.getZone(zoneId).zoneName;
  }

  isTempInRange(temp: number, zone: StorageZone): boolean {
    return temp >= zone.temperatureRange.min && temp <= zone.temperatureRange.max;
  }

  isHumidityInRange(humidity: number, zone: StorageZone): boolean {
    return humidity >= zone.humidityRange.min && humidity <= zone.humidityRange.max;
  }

  isZoneAbnormal(zone: StorageZone): boolean {
    const currentReading = this.getCurrentReading(zone.zoneId);
    return !this.isTempInRange(currentReading.temperature, zone) || 
           !this.isHumidityInRange(currentReading.humidity, zone);
  }

  getTemperatureAlerts(): TemperatureHumidityRecord[] {
    return this.temperatureAlerts.slice(0, 10); // ìµœê·¼ 10ê°œë§Œ
  }

  onTempFilterChange(): void {
    let filtered = [...this.temperatureRecords];

    // ë³´ê´€êµ¬ì—­ í•„í„°
    if (this.tempZoneFilter !== 'all') {
      filtered = filtered.filter(r => r.zoneId === this.tempZoneFilter);
    }

    // ë‚ ì§œ ë²”ìœ„ í•„í„°
    const today = new Date();
    if (this.tempDateRange === 'today') {
      filtered = filtered.filter(r => {
        const recordDate = new Date(r.recordDate);
        return recordDate.toDateString() === today.toDateString();
      });
    } else if (this.tempDateRange === 'week') {
      const weekAgo = new Date(today);
      weekAgo.setDate(weekAgo.getDate() - 7);
      filtered = filtered.filter(r => r.recordDate >= weekAgo);
    } else if (this.tempDateRange === 'month') {
      const monthAgo = new Date(today);
      monthAgo.setDate(monthAgo.getDate() - 30);
      filtered = filtered.filter(r => r.recordDate >= monthAgo);
    }

    // ì´ìƒì¹˜ë§Œ ë³´ê¸°
    if (this.tempShowAbnormalOnly) {
      filtered = filtered.filter(r => r.alertTriggered);
    }

    this.filteredTemperatureRecords = filtered.sort((a, b) => 
      b.recordDate.getTime() - a.recordDate.getTime()
    );
  }

