  // ===== ì¼ë ¨ë²ˆí˜¸ ì¶”ì  ê´€ë¦¬ í•¨ìˆ˜ =====

  serialSearchQuery: string = '';
  serialStatusFilter: string = 'all';
  filteredSerialNumbers: SerialNumber[] = [];

  onSerialSearch(): void {
    let filtered = [...this.serialNumbers];

    // ìƒíƒœ í•„í„°
    if (this.serialStatusFilter !== 'all') {
      filtered = filtered.filter(s => s.status === this.serialStatusFilter);
    }

    // ê²€ìƒ‰ í•„í„°
    if (this.serialSearchQuery.trim()) {
      const query = this.serialSearchQuery.toLowerCase();
      filtered = filtered.filter(s => 
        s.serialNumber.toLowerCase().includes(query) ||
        (s.rfidTag && s.rfidTag.toLowerCase().includes(query)) ||
        s.productName.toLowerCase().includes(query) ||
        s.lotNumber.toLowerCase().includes(query)
      );
    }

    this.filteredSerialNumbers = filtered.sort((a, b) => {
      const aLastEvent = a.trackingHistory.length > 0 ? a.trackingHistory[a.trackingHistory.length - 1].timestamp : new Date(0);
      const bLastEvent = b.trackingHistory.length > 0 ? b.trackingHistory[b.trackingHistory.length - 1].timestamp : new Date(0);
      return bLastEvent.getTime() - aLastEvent.getTime();
    });
  }

  setSerialStatusFilter(status: string): void {
    this.serialStatusFilter = status;
    this.onSerialSearch();
  }

  getSerialNumberCount(status: string): number {
    return this.serialNumbers.filter(s => s.status === status).length;
  }

  getSerialStatusLabel(status: string): string {
    switch(status) {
      case 'available': return 'ì‚¬ìš©ê°€ëŠ¥';
      case 'distributed': return 'ì¶œí•˜ë¨';
      case 'recalled': return 'íšŒìˆ˜ë¨';
      case 'expired': return 'ë§Œë£Œë¨';
      default: return status;
    }
  }

  viewSerialTimeline(serial: SerialNumber): void {
    let message = `ì¼ë ¨ë²ˆí˜¸ ì¶”ì  ì´ë ¥\n\n`;
    message += `ì¼ë ¨ë²ˆí˜¸: ${serial.serialNumber}\n`;
    message += `ì œí’ˆëª…: ${serial.productName}\n`;
    message += `í˜„ì¬ ìƒíƒœ: ${this.getSerialStatusLabel(serial.status)}\n`;
    const lastEvent = serial.trackingHistory.length > 0 ? serial.trackingHistory[serial.trackingHistory.length - 1] : null;
    message += `í˜„ì¬ ìœ„ì¹˜: ${lastEvent?.location || 'ë¯¸ì§€ì •'}\n\n`;
    message += `ì¶”ì  ì´ë²¤íŠ¸ (ì´ ${serial.trackingHistory.length}ê±´):\n\n`;
    
    serial.trackingHistory.forEach((event, index) => {
      message += `${index + 1}. [${event.event}] ${event.timestamp.toLocaleString()}\n`;
      message += `   ìœ„ì¹˜: ${event.location}\n`;
      message += `   ë‹´ë‹¹ì: ${event.user}\n`;
      if (event.notes) {
        message += `   ë¹„ê³ : ${event.notes}\n`;
      }
      message += `\n`;
    });
    
    alert(message);
  }

