import { Injectable } from '@angular/core';
import { CanActivate, Router, ActivatedRouteSnapshot, RouterStateSnapshot, UrlTree } from '@angular/router';
import { Observable } from 'rxjs';
import { ErpAuthService } from '../modules/erp/services/erp-auth.service';

@Injectable({
  providedIn: 'root'
})
export class ErpAuthGuard implements CanActivate {
  constructor(
    private erpAuthService: ErpAuthService,
    private router: Router
  ) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    
    if (this.erpAuthService.isErpLoggedIn) {
      // ê¶Œí•œ ì²´í¬ê°€ í•„ìš”í•œ ê²½ìš°
      const requiredPermission = route.data['permission'] as string;
      if (requiredPermission && !this.erpAuthService.hasPermission(requiredPermission)) {
        this.router.navigate(['/erp/access-denied']);
        return false;
      }
      
      // ì—­í•  ì²´í¬ê°€ í•„ìš”í•œ ê²½ìš°
      const requiredRole = route.data['role'] as string;
      if (requiredRole && !this.erpAuthService.hasRole(requiredRole)) {
        this.router.navigate(['/erp/access-denied']);
        return false;
      }
      
      return true;
    }

    // ë¡œê·¸ì¸ë˜ì–´ ìˆì§€ ì•Šìœ¼ë©´ ë¡œê·¸ì¸ í˜ì´ì§€ë¡œ ë¦¬ë‹¤ì´ë ‰íŠ¸
    return this.router.createUrlTree(['/erp/login'], {
      queryParams: { returnUrl: state.url }
    });
  }
}
