import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

export interface ErpUser {
  id: string;
  username: string;
  employeeId: string;
  department: string;
  role: 'admin' | 'manager' | 'user';
  permissions: string[];
}

@Injectable({
  providedIn: 'root'
})
export class ErpAuthService {
  private currentUserSubject = new BehaviorSubject<ErpUser | null>(null);
  private isErpLoggedInSubject = new BehaviorSubject<boolean>(false);
  
  public currentUser$: Observable<ErpUser | null> = this.currentUserSubject.asObservable();
  public isErpLoggedIn$: Observable<boolean> = this.isErpLoggedInSubject.asObservable();

  constructor() {
    // ERP ë¡œê·¸ì¸ ìƒíƒœ ë³µì›
    const savedUser = localStorage.getItem('erpUser');
    const savedToken = localStorage.getItem('erpToken');
    
    if (savedUser && savedToken) {
      try {
        this.currentUserSubject.next(JSON.parse(savedUser));
        this.isErpLoggedInSubject.next(true);
      } catch (error) {
        this.clearErpAuth();
      }
    }
  }

  get currentUser(): ErpUser | null {
    return this.currentUserSubject.value;
  }

  get isErpLoggedIn(): boolean {
    return this.isErpLoggedInSubject.value;
  }

  /**
   * ERP ì‹œìŠ¤í…œ ë¡œê·¸ì¸
   */
  async erpLogin(username: string, password: string): Promise<boolean> {
    try {
      // ì‹¤ì œ ERP API ë¡œê·¸ì¸ í˜¸ì¶œ
      // const response = await this.http.post('/api/erp/login', { username, password }).toPromise();
      
      // ì„ì‹œ ë¡œê·¸ì¸ ì²˜ë¦¬ (ê°œë°œìš©)
      await new Promise(resolve => setTimeout(resolve, 800));
      
      // ë°ëª¨ìš© ì‚¬ìš©ì ì •ë³´
      const user: ErpUser = {
        id: '1',
        username: username,
        employeeId: 'EMP001',
        department: 'ê´€ë¦¬íŒ€',
        role: 'admin',
        permissions: ['read', 'write', 'delete', 'approve']
      };

      const token = 'erp_token_' + Date.now(); // ì‹¤ì œë¡œëŠ” ì„œë²„ì—ì„œ ë°›ì•„ì˜´

      this.currentUserSubject.next(user);
      this.isErpLoggedInSubject.next(true);
      localStorage.setItem('erpUser', JSON.stringify(user));
      localStorage.setItem('erpToken', token);
      
      return true;
    } catch (error) {
      return false;
    }
  }

  /**
   * íŠ¹ì • ê¶Œí•œ í™•ì¸
   */
  hasPermission(permission: string): boolean {
    const user = this.currentUser;
    return user ? user.permissions.includes(permission) : false;
  }

  /**
   * ì—­í•  í™•ì¸
   */
  hasRole(role: string): boolean {
    const user = this.currentUser;
    return user ? user.role === role : false;
  }

  /**
   * ERP ë¡œê·¸ì•„ì›ƒ
   */
  erpLogout(): void {
    this.currentUserSubject.next(null);
    this.isErpLoggedInSubject.next(false);
    this.clearErpAuth();
  }

  /**
   * ERP í† í° ê°€ì ¸ì˜¤ê¸°
   */
  getErpToken(): string | null {
    return localStorage.getItem('erpToken');
  }

  /**
   * ERP ì¸ì¦ ì •ë³´ ì´ˆê¸°í™”
   */
  private clearErpAuth(): void {
    localStorage.removeItem('erpUser');
    localStorage.removeItem('erpToken');
  }
}
