import { Component, Output, EventEmitter } from '@angular/core';

export type ErpMenuAction = 'toggle-sidebar' | 'toggle-properties' | 'settings';

@Component({
  selector: 'app-erp-vertical-menu',
  templateUrl: './erp-vertical-menu.component.html',
  styleUrls: ['./erp-vertical-menu.component.scss'],
  standalone: false
})
export class ErpVerticalMenuComponent {
  @Output() menuAction = new EventEmitter<ErpMenuAction>();

  onMenuClick(action: ErpMenuAction): void {
    this.menuAction.emit(action);
  }
}
