import { Component, Input, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { ErpAuthService } from '../../services/erp-auth.service';
import { ConfirmationDialogService } from '../../../../shared/services/confirmation-dialog.service';

@Component({
  selector: 'app-erp-header',
  templateUrl: './erp-header.component.html',
  styleUrls: ['./erp-header.component.scss'],
  standalone: false
})
export class ErpHeaderComponent {
  @Input() erpTitle: string = '';
  @Input() activeMenu: string = 'í™ˆ';
  @Input() recordCount: number = 0;
  @Input() isSearchMode: boolean = false;
  @Input() searchResults: any = { current: 0, total: 0 };
  @Input() isProcessing: boolean = false;

  @Output() menuChange = new EventEmitter<string>();
  @Output() toolbarAction = new EventEmitter<string>();
  @Output() search = new EventEmitter<string>();
  @Output() rightPanelToggle = new EventEmitter<void>();
  @Output() leftPanelToggle = new EventEmitter<void>();

  constructor(
    private router: Router,
    public erpAuthService: ErpAuthService,
    private confirmationDialog: ConfirmationDialogService
  ) {}

  onMenuChange(menu: string): void {
    this.menuChange.emit(menu);
  }

  onToolbarAction(action: string): void {
    this.toolbarAction.emit(action);
  }

  onSearch(searchTerm: string): void {
    this.search.emit(searchTerm);
  }

  onRightPanelToggle(): void {
    this.rightPanelToggle.emit();
  }

  onLeftPanelToggle(): void {
    this.leftPanelToggle.emit();
  }

  goToHome(): void {
  }

  logout(): void {
    this.confirmationDialog.confirmLogout().subscribe(result => {
      if (result) {
        this.erpAuthService.erpLogout();
        this.router.navigate(['/erp/login']);
      }
    });
  }
}
